main {
    .product-cart__flex {
        @include block-pos;
        display: flex;
        justify-content: space-between;
        padding: 6px 0 70px;

        .product-cart__wrapper {
            margin: 0;
            margin-right: 120px;

            .product-cart {
                display: flex;
                padding: 0 0 82px;

                .photo__wrapper {
                    width: 342px;
                    height: 342px;
                    padding: 99px;
                    border: 2px solid #000;
                    border-radius: 5px;
                    margin-right: 40px;

                    img {
                        width: 145px;
                    }
                }

                .product-cart__description {
                    padding-top: 15px;
                }

                .product-cart__footer {
                    padding-top: 5px;
                }
            }

            .total-price {
                @include font-params (600, 36px, 44px);

                span {
                    margin-left: 27px;
                    color: #0DA500;
                }
            }
        }

        #order-info {
            display: flex;
            flex-direction: column;

            div {
                display: flex;
                flex-direction: column;
            }

            h2 {
                @include font-params (700, 32px, 39px);
                margin-bottom: 30px;
            }

            label {
                @include font-params (500, 28px, 34px);
                margin-bottom: 30px;
            }

            input[type='checkbox'] {
                position: absolute;
                opacity: 0;
                z-index: -1;
            }

            input[type='checkbox']+label {
                cursor: pointer;
                display: inline-flex;
                align-items: center;

                &::before {
                    content: '';
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    flex-shrink: 0;
                    flex-grow: 0;
                    border: 1px solid #F98600;
                    border-radius: 5px;
                    margin-right: 15px;
                    background-repeat: no-repeat;
                    background-position: center;
                }
            }

            input[type='checkbox']:checked+label::before {
                background-image: url('../../../../public/images/apply-input.svg');
            }

            >textarea {
                border: 1px solid #F98600;
                border-radius: 5px;
                @include font-params(500, 28px, 34px);
                padding: 20px;
                margin-bottom: 30px;
                width: 802px;
                resize: none;
                outline: none;
                overflow-y: hidden;

                &::placeholder {
                    color: #000;
                }

                &.address_textarea {
                    height: 108px;
                }

                &.phone_textarea {
                    height: 74px;
                }

                &.comment_textarea {
                    height: 208px;
                    margin-bottom: 50px;
                }

            }

            input[type='submit'] {
                cursor: pointer;
                border: none;
                margin: auto;
                width: 405px;
                height: 75px;
                background-color: #F98600;
                border-radius: 5px;
                color: #fff;
                @include font-params(500, 28px, 34px);
            }
        }
    }
}

@media only screen and (max-width: 1919px) and (min-width: 1024px) {
    main {
        .product-cart__flex {
            @include block-pos-laptop;
            padding: 0 0 40px;

            .product-cart__wrapper {
                margin-right: 20px;

                .product-cart {
                    padding: 0 0 40px;

                    .photo__wrapper {
                        width: 137px;
                        height: 137px;
                        padding: 34px;
                        margin-right: 20px;

                        img {
                            width: 68px;
                        }
                    }

                    .product-cart__description {
                        padding-top: 0;

                        h2 {
                            @include font-params (600, 22px, 27px);
                            margin-bottom: 15px;
                        }

                        p {
                            @include font-params (400, 18px, 22px);
                            margin-bottom: 10px;

                        }
                    }

                    .product-cart__footer {
                        padding-top: 5px;

                        >div {
                            p {
                                margin-bottom: 0;
                            }

                            button {
                                padding: 10px 20px 10px 10px;

                                &:last-child {
                                    padding: 10px 10px 10px 20px;
                                    transform: translateY(-1px);
                                }
                            }
                        }
                    }
                }

                .total-price {
                    @include font-params (600, 22px, 27px);

                    span {
                        font-weight: 400;
                        margin-left: 27px;
                    }
                }
            }

            #order-info {
                h2 {
                    @include font-params (700, 22px, 27px);
                    margin-bottom: 20px;
                }

                label {
                    @include font-params (500, 20px, 24px);
                    margin-bottom: 15px;
                }

                input[type='checkbox']+label {
                    &::before {
                        width: 30px;
                        height: 30px;
                        background-size: 25px;
                    }
                }

                >textarea {
                    @include font-params(500, 20px, 24px);
                    padding: 10px;
                    margin-bottom: 20px;
                    width: 452px;

                    &.address_textarea {
                        height: 80px;
                    }

                    &.phone_textarea {
                        height: 44px;
                    }

                    &.comment_textarea {
                        height: 119px;
                        margin-bottom: 40px;
                    }

                }

                input[type='submit'] {
                    width: 292px;
                    height: 68px;
                    @include font-params(500, 22px, 27px);
                }
            }
        }
    }
}

@media only screen and (max-width: 1023px) and (min-width: 280px) {
    main {
        .product-cart__flex {
            @include block-pos-mobile;
            padding: 0 0 20px;
            flex-direction: column;

            .product-cart__wrapper {
                margin-right: 0;

                .product-cart {
                    padding: 0 0 40px;

                    .photo__wrapper {
                        width: 154px;
                        height: 136px;
                        padding: 28px 37px;
                        margin-right: 0;
                        margin-bottom: 15px;

                        img {
                            width: 80px;
                        }
                    }

                    .product-cart__description {
                        padding-top: 0;

                        h2 {
                            @include font-params (600, 16px, 19px);
                            margin-bottom: 10px;
                        }

                        p {
                            @include font-params (400, 14px, 17px);
                            margin-bottom: 10px;

                        }
                    }

                    .product-cart__footer {
                        padding-top: 10px;

                        p {
                            margin-bottom: 0;
                        }

                        >div {
                            button {
                                padding: 4px 22px 4px 5px;

                                &:last-child {
                                    padding: 4px 5px 4px 22px;
                                    transform: translateY(-1px);
                                }
                            }
                        }
                    }
                }

                .total-price {
                    @include font-params (600, 16px, 19px);

                    span {
                        font-weight: 500;
                        margin-left: 20px;
                    }
                }
            }

            #order-info {
                h2 {
                    @include font-params (700, 16px, 19px);
                    margin-bottom: 15px;
                }

                label {
                    @include font-params (500, 14px, 17px);
                    margin-bottom: 15px;
                }

                input[type='checkbox']+label {
                    &::before {
                        width: 25px;
                        height: 25px;
                        background-size: 20px;
                    }
                }

                >textarea {
                    @include font-params(500, 14px, 17px);
                    padding: 10px;
                    margin-bottom: 15px;
                    width: 330px;

                    &.address_textarea {
                        height: 80px;
                    }

                    &.phone_textarea {
                        height: 37px;
                    }

                    &.comment_textarea {
                        height: 119px;
                        margin-bottom: 20px;
                    }

                }

                input[type='submit'] {
                    width: 207px;
                    height: 48px;
                    @include font-params(500, 16px, 19px);
                }
            }
        }
    }
}