.model-catalog {
    @include block-pos;
    padding: 6px 0 70px;

    >h1 {
        text-transform: uppercase;
        margin-bottom: 40px;
    }

    .model-catalog__wrapper {
        display: grid;
        grid-template-columns: 409px 1271px;
        grid-gap: 30px;

        .model-catalog__mobile_button {
            display: none;
        }

        aside {
            @include font-params (500, 26px, 31px);
            padding: 30px 15px;
            border: 1px solid #F98600;
            border-radius: 5px;
            height: max-content;

            ul {
                li {
                    margin-bottom: 15px;
                    position: relative;

                    &.open {
                        color: #F98600;

                        li {
                            color: $link_color;
                        }
                    }

                    .model-catalog_button {
                        width: 100%;
                        height: 30px;
                        position: absolute;
                        background: url('../../../../public/images/accordion-catalog-arrow.svg') no-repeat right;
                        background-size: 15px;
                        top: 0;
                        right: 0;
                        cursor: pointer;
                        z-index: 1;
                        outline: none;

                        &.active {
                            top: 100%;
                            background: url('../../../../public/images/hidden-button.svg') no-repeat right;
                            background-size: 100px;
                            background-position-x: 90%;
                            transform: translateY(-100%);

                            span {
                                display: block;
                                position: absolute;
                                top: 0;
                                right: 0;
                                width: 15px;
                                height: 30px;
                                background: url('../../../../public/images/accordion-catalog-arrow.svg') no-repeat center;
                                background-size: 15px;
                                background-position-x: 0px;
                                transform: rotate(180deg);
                                z-index: 0;
                            }
                        }
                    }

                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }

            .spares-brand {
                margin-top: 10px;
                padding-left: 20px;
                padding-bottom: 40px;
                border-left: 1px solid #F98600;

                li {
                    &.open {
                        color: #F98600;

                        li {
                            color: $link_color;
                        }
                    }
                }

                .spares-categories {
                    margin-top: 10px;
                    padding-left: 20px;
                    padding-bottom: 40px;
                    border-left: 1px solid #F98600;

                    li {
                        color: $link_color;
                        cursor: pointer;

                        &.active {
                            color: #F98600;
                        }
                    }
                }
            }
        }

        >div {
            .product-group__wrapper {
                border: 1px solid #F98600;
                border-radius: 5px;
                padding: 20px;
                position: relative;
                margin-bottom: 30px;

                .product-group {
                    display: flex;
                    justify-content: space-between;

                    >div {

                        h2 {
                            @include font-params(600, 28px, 34px);
                            margin-bottom: 30px;
                        }

                        p {
                            @include font-params(500, 28px, 34px);
                            margin-bottom: 20px;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }

                    button {
                        color: #F98600;
                        @include font-params(600, 28px, 34px);
                        position: absolute;
                        right: 20px;
                        bottom: 20px;

                        &::after {
                            content: '';
                            width: 20px;
                            height: 20px;
                            display: inline-block;
                            background: url('../../../../public/images/accordion-catalog-arrow.svg') no-repeat center;
                            vertical-align: middle;
                            margin-left: 15px;
                        }

                        &.active {
                            &::after {
                                transform: rotate(180deg);
                            }

                        }
                    }
                }

                .analogue-products {
                    padding-top: 45px;
                    padding-left: 50px;

                    &.open {
                        display: block;

                        .analogue__wrapper {
                            display: flex;
                        }
                    }

                    &.hidden {
                        display: none;
                    }

                    >div {
                        margin-bottom: 70px;
                    }

                    .analogue__wrapper {
                        display: none;
                        align-items: center;

                        .photo__wrapper {
                            border: 2px solid $link_color;
                            border-radius: 5px;
                            padding: 31px 46px;
                            margin-right: 27px;

                            img {
                                width: 100px;
                            }
                        }

                        h2 {
                            @include font-params(600, 28px, 34px);
                            margin-bottom: 30px;
                        }

                        p {
                            @include font-params(500, 28px, 34px);
                            margin-bottom: 15px;

                            &.price {
                                margin-top: 45px;
                                margin-bottom: 0;
                                color: #0DA500;
                            }
                        }
                    }

                    >div {
                        position: relative;

                        button {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            color: #F98600;
                            @include font-params(500, 28px, 34px);

                            &::after {
                                content: '';
                                width: 20px;
                                height: 20px;
                                display: inline-block;
                                background: url('../../../../public/images/accordion-catalog-arrow.svg') no-repeat center;
                                vertical-align: middle;
                                margin-left: 15px;
                            }

                            &.active {
                                &::after {
                                    transform: rotate(180deg);
                                }

                            }
                        }
                    }

                    .in-stock {
                        padding-left: 222px;
                        padding-top: 45px;

                        &.hidden {
                            display: none;
                        }

                        &.open {
                            display: flex;
                            flex-direction: column;
                            padding-bottom: 50px;
                        }

                        h2 {
                            @include font-params(500, 32px, 39px);
                            margin-bottom: 20px;
                        }

                        p {
                            display: flex;
                            justify-content: space-between;
                            @include font-params(400, 28px, 34px);
                            margin-bottom: 20px;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1919px) and (min-width: 1024px) {
    .model-catalog {
        @include block-pos-laptop;
        padding: 0 0 40px;

        >h1 {
            text-transform: uppercase;
            margin-bottom: 30px;
        }

        .model-catalog__wrapper {
            display: grid;
            grid-template-columns: 305px 602px;
            grid-gap: 27px;

            aside {
                @include font-params (500, 20px, 24px);

                ul {
                    li {
                        .model-catalog_button {
                            height: 24px;
                            background-size: 12px;

                            &.active {
                                background-size: 80px;
                                background-position-x: 90%;
                                transform: translateY(-100%);

                                span {
                                    height: 24px;
                                    background-size: 12px;
                                }
                            }
                        }
                    }
                }
            }

            >div {
                .product-group__wrapper {
                    padding: 20px 10px;

                    .product-group {
                        >div {
                            h2 {
                                @include font-params(600, 20px, 24px);
                                margin-bottom: 15px;
                            }

                            p {
                                @include font-params(500, 20px, 24px);
                                margin-bottom: 15px;
                            }
                        }

                        button {
                            @include font-params(500, 20px, 24px);
                            right: 10px;

                            &::after {
                                width: 15px;
                                height: 15px;
                                margin-left: 15px;
                            }
                        }
                    }

                    .analogue-products {
                        padding-top: 20px;
                        padding-left: 0;

                        >div {
                            margin-bottom: 30px;
                        }

                        .analogue__wrapper {
                            align-items: flex-start;
                            padding-bottom: 20px;

                            .photo__wrapper {
                                padding: 18px 22px;
                                margin-right: 15px;

                                img {
                                    width: 48px;
                                }
                            }

                            h2 {
                                @include font-params(600, 20px, 24px);
                                margin-bottom: 15px;
                            }

                            p {
                                @include font-params(500, 20px, 24px);
                                margin-bottom: 15px;

                                &.price {
                                    margin: 0;
                                }
                            }
                        }

                        >div {

                            button {
                                @include font-params(400, 20px, 24px);

                                &::after {
                                    width: 15px;
                                    height: 15px;
                                }
                            }
                        }

                        .in-stock {
                            padding-left: 121px;
                            padding-top: 0px;

                            h2 {
                                @include font-params(500, 22px, 27px);
                                margin-bottom: 15px;
                            }

                            p {
                                @include font-params(400, 16px, 19px);
                                margin-bottom: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1023px) and (min-width: 280px) {
    .model-catalog {
        @include block-pos;
        padding: 0 0 40px;

        >h1 {
            @include font-params (600, 14px, 17px);
            text-transform: uppercase;
            margin-bottom: 10px;
        }

        .model-catalog__wrapper {
            display: grid;
            grid-template-columns: 330px;
            grid-gap: 20px;

            .model-catalog__mobile_button {
                display: block;
                @include font-params (500, 14px, 17px);
                padding: 12px 0;
                border: 1px solid #F98600;
                border-radius: 5px;

                &.active {
                    background-color: #F98600;
                    color: #fff;
                }
            }

            aside {
                display: none;
                @include font-params (500, 16px, 19px);
                padding: 20px 15px;

                &.open {
                    display: block;
                }

                ul {
                    li {
                        margin-bottom: 15px;

                        .model-catalog_button {
                            height: 19px;
                            background-size: 10px;

                            &.active {
                                background-size: 60px;
                                background-position-x: 92%;

                                span {
                                    width: 10px;
                                    height: 19px;
                                    background-size: 10px;
                                }
                            }
                        }
                    }
                }

                .spares-brand {
                    padding-bottom: 25px;

                    .spares-categories {
                        padding-bottom: 25px;
                    }
                }
            }

            >div {
                .product-group__wrapper {
                    padding: 10px 10px 43px;
                    position: relative;
                    margin-bottom: 20px;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    .product-group {
                        >div {

                            h2 {
                                @include font-params(600, 14px, 17px);
                                margin-bottom: 10px;
                            }

                            p {
                                @include font-params(500, 14px, 17px);
                                margin-bottom: 10px;

                                &:last-child {
                                    margin-bottom: 0;
                                }
                            }
                        }

                        button {
                            @include font-params(600, 16px, 19px);
                            right: 10px;
                            bottom: 10px;

                            &::after {
                                width: 10px;
                                height: 19px;
                                background-size: 10px;
                                margin-left: 10px;
                            }
                        }
                    }

                    .analogue-products {
                        padding-top: 20px;
                        padding-left: 0;

                        >div {
                            margin-bottom: 20px;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }

                        .analogue__wrapper {
                            align-items: flex-start;

                            .photo__wrapper {
                                padding: 14px 16px;
                                margin-right: 27px;

                                img {
                                    width: 36px;
                                }
                            }

                            h2 {
                                @include font-params(600, 14px, 17px);
                                margin-bottom: 10px;
                            }

                            p {
                                @include font-params(500, 14px, 17px);
                                margin-bottom: 10px;

                                &.price {
                                    @include font-params(500, 16px, 19px);
                                    margin-top: 15px;
                                    margin-bottom: 27px;
                                }
                            }
                        }

                        >div {
                            button {
                                @include font-params(500, 16px, 19px);

                                &::after {
                                    width: 10px;
                                    height: 19px;
                                    margin-left: 10px;
                                }
                            }
                        }

                        .in-stock {
                            padding-left: 0;
                            padding-top: 0;

                            &.hidden {
                                display: none;
                            }

                            &.open {
                                display: flex;
                                flex-direction: column;
                                padding-bottom: 33px;
                            }

                            h2 {
                                @include font-params(500, 16px, 19px);
                                margin-bottom: 10px;
                            }

                            p {
                                @include font-params(400, 10px, 15px);
                                margin-bottom: 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}