.orders__flex {
    @include block-pos;

    .orders__wrapper {
        margin: 0;
        flex-direction: column;

        .order {
            margin-bottom: 43px;
            position: relative;

            &:last-child {
                margin-bottom: 0;
            }

            h2 {
                cursor: pointer;
                margin-bottom: 20px;
                @include font-params (600, 32px, 39px);

                &::before {
                    display: inline-block;
                    content: '';
                    width: 40px;
                    height: 40px;
                    background: url('../../../../public/images/select-arrow.svg') no-repeat center;
                    background-size: contain;
                    transform: rotate(-90deg);
                    vertical-align: middle;
                    margin-right: 20px;
                }

                &.active {
                    &::before {
                        transform: rotate(0);
                    }
                }
            }

            .order-status {
                position: absolute;
                right: 0;
                top: 0;
                width: 220px;
                background-color: #FFE976;
                border-radius: 5px;
                text-align: center;
                padding: 11px 0;
                @include font-params (500, 28px, 34px);
            }

            h3 {
                margin-bottom: 20px;
                @include font-params (500, 28px, 34px);
            }

            p {
                margin-bottom: 15px;
                @include font-params (400, 28px, 34px);
            }

            .order__details {
                padding-left: 15px;
                max-width: 700px;
            }
        }
    }
}

@media only screen and (max-width: 1919px) and (min-width: 1024px) {
    .orders__flex {
        @include block-pos-laptop;

        .orders__wrapper {
            .order {
                margin-bottom: 40px;

                &:last-child {
                    margin-bottom: 0;
                }

                h2 {
                    margin-bottom: 18px;
                    @include font-params (600, 22px, 27px);

                    &::before {
                        width: 30px;
                        height: 30px;
                        margin-right: 20px;
                    }
                }

                .order-status {
                    width: 165px;
                    @include font-params (500, 20px, 24px);
                }

                h3 {
                    margin-bottom: 15px;
                    @include font-params (500, 20px, 24px);
                }

                p {
                    @include font-params (400, 18px, 22px);
                }

                .order__details {
                    max-width: 400px;
                }
            }
        }
    }
}

@media only screen and (max-width: 1023px) and (min-width: 280px) {
    .orders__flex {
        @include block-pos-mobile;

        .orders__wrapper {

            .order {
                margin-bottom: 24px;

                &:last-child {
                    margin-bottom: 0;
                }

                h2 {
                    margin-bottom: 18px;
                    @include font-params (600, 16px, 19px);

                    &::before {
                        width: 20px;
                        height: 20px;
                        margin-right: 15px;
                    }
                }

                .order-status {
                    width: 110px;
                    padding: 7px 0;
                    @include font-params (500, 14px, 17px);
                }

                h3 {
                    margin-bottom: 10px;
                    @include font-params (500, 16px, 19px);
                }

                p {
                    margin-bottom: 10px;
                    @include font-params (400, 14px, 17px);
                }

                .order__details {
                    padding-left: 15px;
                    max-width: 200px;
                }
            }
        }
    }
}