.product-cart__wrapper {
    @include block-pos;
    padding-bottom: 70px;

    .product-cart {
        display: flex;
        padding: 6px 0 50px;

        .photo__wrapper {
            width: 449px;
            height: 449px;
            padding: 130px;
            border: 2px solid #000;
            margin-right: 107px;
            border-radius: 5px;

            img {
                width: 190px;
            }
        }

        .product-cart__description {
            @include font-params(500, 28px, 34px);
            padding-top: 50px;

            h2 {
                @include font-params(600, 28px, 34px);
                margin-bottom: 30px;
            }

            p {
                margin-bottom: 15px;

                span {
                    @include font-params(400, 28px, 34px);

                }
            }

        }

        .product-cart__footer {
            display: flex;
            align-items: center;
            padding-top: 44px;
            justify-content: space-between;

            p {
                @include font-params(500, 36px, 44px);
                color: #0DA500;
                margin: 0;
            }

            >div {
                display: flex;
                align-items: center;
                border: 1px solid #F98600;
                border-radius: 5px;

                p {
                    color: $link_color;
                    margin: 0;
                }

                button {
                    @include font-params(400, 36px, 44px);
                    padding: 16px 25px 15px 20px;

                    &:last-child {
                        padding: 15px 20px 16px 25px;

                    }
                }
            }

            >button {
                @include font-params(500, 28px, 34px);
                color: #fff;
                background-color: #F98600;
                border-radius: 5px;
                width: 345px;
                height: 75px;
            }
        }
    }

    .analogue-product__flex {
        display: flex;
        flex-direction: column;

        h2 {
            text-align: center;
            @include font-params(700, 32px, 39px);
            margin-bottom: 35px;
        }

        >div {
            position: relative;
            margin-bottom: 50px;

            .analogue__wrapper {
                display: flex;
                align-items: center;

                .photo__wrapper {
                    border: 2px solid $link_color;
                    border-radius: 5px;
                    padding: 49px 65px;
                    margin-right: 50px;

                    img {
                        width: 142px;
                    }
                }

                h2 {
                    @include font-params(600, 28px, 34px);
                    margin-bottom: 30px;
                }

                p {
                    @include font-params(500, 28px, 34px);
                    margin-bottom: 15px;

                    &.price {
                        margin-top: 45px;
                        margin-bottom: 0;
                        color: #0DA500;
                    }
                }
            }

            button {
                position: absolute;
                right: 0;
                bottom: 0;
                color: #F98600;
                @include font-params(500, 28px, 34px);

                &::after {
                    content: '';
                    width: 20px;
                    height: 20px;
                    display: inline-block;
                    background: url('../../../../public/images/accordion-catalog-arrow.svg') no-repeat center;
                    vertical-align: middle;
                    margin-left: 15px;
                }

                &.active {
                    &::after {
                        transform: rotate(180deg);
                    }

                }
            }

            .in-stock {
                padding-left: 222px;
                padding-top: 45px;

                &.hidden {
                    display: none;
                }

                &.open {
                    display: flex;
                    flex-direction: column;
                    padding-bottom: 50px;
                }

                h2 {
                    @include font-params(500, 32px, 39px);
                    margin-bottom: 20px;
                }

                p {
                    display: flex;
                    justify-content: space-between;
                    @include font-params(400, 28px, 34px);
                    margin-bottom: 20px;

                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }

        >button {
            background-color: #F98600;
            border-radius: 5px;
            color: #fff;
            width: 363px;
            height: 63px;
            margin: 0 auto;
            @include font-params(500, 28px, 34px);
        }
    }
}

@media only screen and (max-width: 1919px) and (min-width: 1024px) {
    .product-cart__wrapper {
        @include block-pos-laptop;
        padding-bottom: 40px;

        .product-cart {
            display: flex;
            padding: 0 0 40px;

            .photo__wrapper {
                width: 235px;
                height: 235px;
                padding: 68px;
                margin-right: 30px;

                img {
                    width: 100px;
                }
            }

            .product-cart__description {
                @include font-params(500, 20px, 24px);
                padding-top: 0;

                h2 {
                    @include font-params(600, 20px, 24px);
                    margin-bottom: 20px;
                }

                p {
                    margin-bottom: 15px;

                    span {
                        @include font-params(400, 20px, 24px);

                    }
                }

            }

            .product-cart__footer {
                padding-top: 30px;

                p {
                    @include font-params(500, 22px, 27px);
                    margin-bottom: 0;
                }

                >div {
                    button {
                        @include font-params(400, 22px, 27px);
                        padding: 10px 31px 10px 10px;

                        &:last-child {
                            padding: 10px 10px 10px 31px;

                        }
                    }
                }

                >button {
                    @include font-params(500, 22px, 27px);
                    width: 221px;
                    height: 68px;
                }
            }
        }

        .analogue-product__flex {
            h2 {
                @include font-params(700, 20px, 24px);
                margin-bottom: 40px;
            }

            >div {
                position: relative;
                margin-bottom: 50px;

                .analogue__wrapper {

                    .photo__wrapper {
                        padding: 35px 45px;
                        margin-right: 30px;

                        img {
                            width: 97px;
                        }
                    }

                    h2 {
                        @include font-params(600, 20px, 24px);
                        margin-bottom: 15px;
                    }

                    p {
                        @include font-params(500, 20px, 24px);
                        margin-bottom: 15px;

                        &.price {
                            margin-top: 0;
                            margin-bottom: 43px;
                        }
                    }
                }

                button {
                    @include font-params(500, 22px, 27px);

                    &::after {
                        width: 15px;
                        height: 20px;
                        background-size: 15px;
                    }
                }

                .in-stock {
                    padding-top: 0;

                    &.open {
                        padding-bottom: 43px;
                    }

                    h2 {
                        text-align: left;
                        @include font-params(500, 22px, 27px);
                        margin-bottom: 15px;
                    }

                    p {
                        @include font-params(400, 20px, 24px);
                        margin-bottom: 15px;
                    }
                }
            }

            >button {
                width: 270px;
                height: 40px;
                @include font-params(500, 20px, 24px);
            }
        }
    }
}

@media only screen and (max-width: 1023px) and (min-width: 280px) {
    .product-cart__wrapper {
        @include block-pos-mobile;
        padding-bottom: 20px;

        .product-cart {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 0 0 40px;

            .photo__wrapper {
                width: 154px;
                height: 136px;
                padding: 28px 37px;
                margin-right: 0;
                margin-bottom: 15px;

                img {
                    width: 80px;
                }
            }

            .product-cart__description {
                @include font-params(500, 14px, 17px);
                max-width: 330px;
                padding-top: 0;

                h2 {
                    @include font-params(600, 16px, 19px);
                    margin-bottom: 10px;
                }

                p {
                    margin-bottom: 10px;

                    span {
                        @include font-params(400, 14px, 17px);

                    }
                }

            }

            .product-cart__footer {
                padding-top: 15px;

                p {
                    @include font-params(500, 16px, 19px);
                    margin-bottom: 0;
                }

                >div {
                    button {
                        @include font-params(400, 22px, 27px);
                        padding: 5px 22px 5px 5px;

                        &:last-child {
                            padding: 5px 5px 5px 22px;
                            transform: translateY(-1px);
                        }
                    }
                }

                >button {
                    @include font-params(500, 16px, 19px);
                    width: 118px;
                    height: 39px;
                }
            }
        }

        .analogue-product__flex {
            h2 {
                @include font-params(700, 16px, 19px);
                margin-bottom: 30px;
            }

            >div {
                margin-bottom: 20px;

                .analogue__wrapper {
                    align-items: flex-start;

                    .photo__wrapper {
                        padding: 14px 17px;
                        margin-right: 15px;

                        img {
                            width: 36px;
                        }
                    }

                    h2 {
                        @include font-params(600, 14px, 17px);
                        margin-bottom: 10px;
                        text-align: left;
                    }

                    p {
                        @include font-params(500, 14px, 17px);
                        margin-bottom: 10px;

                        &.price {
                            margin-top: 15px;
                            margin-bottom: 34px;
                        }
                    }
                }

                button {
                    @include font-params(500, 16px, 19px);

                    &::after {
                        width: 10px;
                        height: 19px;
                        background-size: 10px;
                        margin-left: 10px;
                    }
                }

                .in-stock {
                    padding: 0;

                    &.open {
                        padding-bottom: 34px;
                    }

                    h2 {
                        text-align: left;
                        @include font-params(500, 16px, 19px);
                        margin-bottom: 10px;
                    }

                    p {
                        @include font-params(400, 11px, 15px);
                        margin-bottom: 10px;
                    }
                }
            }

            >button {
                width: 170px;
                height: 40px;
                @include font-params(500, 14px, 17px);
            }
        }
    }
}