.news {
    @include block-pos;
    padding: 30px 0 70px;

    p {
        @include font-params (400, 28px, 34px);
        margin-bottom: 20px;
    }



    .article {
        display: grid;
        grid-gap: 50px;
        grid-template-rows: 740px 740px 740px;

        .news_title {
            h2 {
                @include font-params (700, 32px, 39px);
                margin-bottom: 20px;
            }

            &.mobile {
                display: none;
            }
        }

        .article_block {
            display: grid;
            grid-template-columns: 1018px 646px;
            grid-gap: 45px;

            img {
                grid-column: 2;
                grid-row: 1;
            }

            p {
                grid-column: 1;
            }

            &:nth-child(2),
            &:last-child {
                grid-template-columns: 646px 1018px;

                img {
                    grid-column: 1;
                }

                p {
                    grid-column: 2;
                }
            }
        }
    }
}

@media only screen and (max-width: 1919px) and (min-width: 1024px) {
    .news {
        @include block-pos-laptop;
        padding: 27px 0 40px;

        p {
            @include font-params (400, 20px, 24px);
            margin-bottom: 10px;
        }

        .article {
            grid-gap: 20px;
            grid-template-rows: 609px 812px 678px;

            .news_title {

                h2 {
                    @include font-params (700, 22px, 27px);
                    margin-bottom: 10px;
                }

                p {
                    @include font-params (400, 16px, 19px);
                }
            }

            .article_block {
                grid-template-columns: 456px 448px;
                grid-gap: 20px;

                img {
                    height: 812px;
                    width: 448px;
                }

                &:nth-child(2),
                &:last-child {
                    grid-template-columns: 373px 531px;

                    img {
                        height: 609px;
                        width: 373px;
                    }
                }

                &:last-child {
                    img {
                        height: 678px;
                    }
                }
            }
        }
    }

}

@media only screen and (max-width: 1023px) and (min-width: 280px) {
    .news {
        @include block-pos-mobile;
        padding: 98px 0 20px;

        p {
            @include font-params (400, 14px, 17px);
            margin-bottom: 10px;
            grid-row: 3;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .article {
            display: grid;
            grid-gap: 10px;
            grid-template-rows: none;

            .news_title {
                display: none;
                grid-row: 1;

                h2 {
                    @include font-params (700, 16px, 19px);
                    margin-bottom: 10px;
                }

                p {
                    @include font-params (400, 10px, 12px);
                }

                &.mobile {
                    display: block;
                    grid-row: 1;
                }
            }

            .article_block {
                display: grid;
                grid-template-columns: 330px;
                grid-gap: 10px;

                img {
                    grid-column: 1;
                    grid-row: 1;
                    width: 330px;
                    height: 211px;
                }

                p {
                    grid-column: 1;
                    grid-row: 2;
                }

                &:nth-child(2),
                &:last-child {
                    grid-template-columns: 330px;

                    img {
                        grid-column: 1;
                        grid-row: 1;
                    }

                    p {
                        grid-column: 1;
                        grid-row: 2;
                    }
                }
            }
        }
    }
}